.ico {
    background-repeat: no-repeat;
    @include sprite();
}
.ico-after {
    position: relative;

    &:after {
        content: " ";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 16px;
        height: 16px;
        background-repeat: no-repeat;
        @include sprite();
    }

    &.alert, &.ico-alert {
        &:after {
            @include sprite-position($alert);
        }
        &.blue:after {
            @include sprite-position($alert-blue);
        }
        &.light:after {
            @include sprite-position($alert-light);
        }
    }

    &.arrow-left {
        &:after {
            @include sprite-position($arrow-left);
        }
        &.blue:after {
